	module.exports = {"data":{"markdownRemark":{"html":"<h2>XSS Cross Site Scripting</h2>\n<p>Cross Site Scripting (XSS) is is a type of code injection. It is one of the most common vulnerabilities on the web. </p>\n<p>XSS usually involves entering code into an form, url parameter or anywhere else that accepts user input that is displayed back to users. For example imagine a website that showed your user profile, and you entered <code>&#x3C;script>alert(\"HELLO!\");&#x3C;/script></code> as your username. If the site did not prevent XSS, then every person that visited your profile would get an alert pop-up.</p>\n<h4>Different types of XSS</h4>\n<p>There are 3 different types of XSS.</p>\n<ul>\n<li>Stored XSS</li>\n<li>Reflective XSS</li>\n<li>DOM Based XSS</li>\n</ul>\n<h4>Dangers of XSS</h4>\n<p>Of course, the above example does not present any immediate danger to those viewing your profile. But, what if you had more sinister intentions? You could execute some javascript to show a fake log in page, and gather usernames and passwords for other users of the site, or simply read the session cookie if it is not secure. You could force them to visit other websites or perform an action. </p>\n<h4>Defend against XSS</h4>\n<ul>\n<li>Never trust user data</li>\n<li>Validate untrusted data (check for valid data, if invalid, reject rather than process)</li>\n<li>Whitelist safe values (rather than blacklist)</li>\n<li>Always encode output</li>\n<li>Encode for the correct context (HTML / HTML attributes / CSS / JSS are all different)</li>\n<li>Protect cookies (HTTP only and secure can only be read by the server)</li>\n<li>Implement a Content-Security Policy</li>\n</ul>\n<h4>More Information:</h4>\n<p>Read on more information on XSS and the steps you can take to protect against is from:</p>\n<p><a href='https://www.owasp.org/index.php/Cross-site_Scripting_(XSS' target='_blank' rel='nofollow'>Cross-site Scripting (XSS)</a>)</p>\n<p><a href='https://www.owasp.org/index.php/XSS_(Cross_Site_Scripting' target='_blank' rel='nofollow'>XSS (Cross Site Scripting) Prevention Cheat Sheet</a><em>Prevention</em>Cheat_Sheet)</p>\n<p><a href='https://www.hacksplaining.com/exercises/xss-stored' target='_blank' rel='nofollow'>Hacksplaining XSS Lesson</a></p>","fields":{"slug":"/security/xss-cross-site-scripting/"},"frontmatter":{"title":"XSS Cross Site Scripting"}}},"pathContext":{"slug":"/security/xss-cross-site-scripting/"}}

